import { WidgetManager } from '@engine/utils';
import { PageResult } from './table.types';

export class CellCacheManager extends WidgetManager<Widget> {
    public static NAME = 'CELL_CACHE';

    private updatedCellValueCache: PageResult[] = [];

    public addCellCache(cellData: PageResult) {
        this.updatedCellValueCache.push(cellData);
    }

    public getCellCache() {
        return this.updatedCellValueCache;
    }

    private clearCellCache() {
        this.updatedCellValueCache = [];
    }

    /**
     * 在resize时，不能够去掉cache信息，否则失焦时会无法将值更新上去
     * 修复 https://work.fineres.com/browse/REPORT-73661 删除
     */
    public update() {}

    /**
     * 清除字典
     */
    public clear() {
        this.clearCellCache();

        super.clear();
    }
}
